<template>
    <div>
        <notifications group="notifications-default" />
        <h4 class="font-weight-bold py-3 mb-4">Registro de Personal</h4>
        <div class="w-100 text-right"></div>
        
        <div class="row">
            <div class="col">
                <!-- OPCIONES DE BUSQUEDA Y BOTONES PARA CREAR NUEVOS REGISTROS -->
            <b-row>

                <!-- ESTE ES EL BUSCADOR Y EL BOTON PARA BORRAR -->
                <b-col md="5" class="my-1">
                    <b-input-group>
                        <b-form-input v-model="filter" placeholder="Buscar..." />
                        <b-input-group-append>
                        <b-btn :disabled="!filter" @click="filter = ''"> <span class="oi oi-delete" ></span> </b-btn>
                        </b-input-group-append>
                    </b-input-group>
                </b-col>
                <!-- ESTE ES EL BUSCADOR Y EL BOTON PARA BORRAR -->

                <!-- ESTE ES EL PAGINADOR -->
                <b-col md="5" class="my-1">
                    <b-form-select :options="pageOptions" v-model="perPage" />
                </b-col>
                <!-- ESTE ES EL PAGINADOR -->

                <!-- BOTONES PARA CREAR NUEVOS REGISTROS Y PARA OPCION DE AYUDA -->
                <b-col md="2" class="d-flex justify-content-center my-1">
                    <b-btn variant="outline-success icon-btn" class="btn-md" v-if="check_access('admin_personal', 'c')"  v-b-modal.modals-default @click.prevent="addData();objState={}"><i class="ion ion-md-add"></i></b-btn>&nbsp;
                    <b-btn variant="outline-info icon-btn" class="btn-md"  v-b-modal.modals-default @click.prevent="openHelp"><i class="fas fa-chalkboard-teacher"></i></b-btn>
                </b-col>
                <!-- BOTONES PARA CREAR NUEVOS REGISTROS Y PARA OPCION DE AYUDA -->
            </b-row>
            <!-- OPCIONES DE BUSQUEDA Y BOTONES PARA CREAR NUEVOS REGISTROS -->
            
                <b-table small show-empty stacked="md" :items="tableData" :fields="columns" :current-page="currentPage" :per-page="perPage" 
                    :filter="filter" @filtered="onFiltered">
                    <template v-slot:cell(edit)="row">
                        <div class="text-left">
                            <b-btn variant="outline-success borderless icon-btn" class="btn-xs" v-if="check_access('admin_personal', 'c')" @click.prevent="data_change(row.item,'edit')"><i class="ion ion-md-create"></i></b-btn>
                            <b-btn variant="outline-danger borderless icon-btn" class="btn-xs" @click.prevent="data_change(row.item,'delete')"><i class="ion ion-md-close"></i></b-btn>
                            <b-btn variant="outline-info borderless icon-btn" class="btn-xs" @click.prevent="showTracking(row.item)"><i class="ion ion-md-eye"></i></b-btn>
                        </div>
                    </template>
                    <template v-slot:cell(show)="row">
                        <div class="text-left">
                            <!-- <i class="ion ion-md-create"></i> -->
                        <span style="color:red" v-if="!row.item.show"><i class="ion ion-md-remove-circle"></i></span>
                            <span style="color:green" v-if="row.item.show"><i class="ion ion-md-add-circle"></i></span>
                        </div>
                    </template>
                    <template v-slot:cell(active)="row">
                        <div class="text-left">
                            <!-- <i class="ion ion-md-create"></i> -->
                            <span style="color:red" v-if="!row.item.active"><i class="ion ion-md-remove-circle"></i></span>
                            <span style="color:green" v-if="row.item.active"><i class="ion ion-md-add-circle"></i></span>
                        </div>
                    </template>
                </b-table>
                 <div class="d-flex justify-content-between">
                    <div>
                        <b-pagination :total-rows="totalRows" :per-page="perPage" v-model="currentPage" class="my-0" />
                    </div>
                    <div class="text-muted">
                        <small> Total Operarios: {{this.totalRows}} </small>
                    </div>
                </div>
            </div>
        </div>

        <div class="d-inline-block">
            <sweet-modal icon="error" title="Borrar registro" ref="errorWithButtonAlert">
                Est seguro que desea borrar el registro?
                <div class="w-100 text-center">
                    El registro ser borrado de forma lgica, pero su informacin ser visible en reportes. 
                </div>
                
                <template slot="button"><b-btn @click="deleteRecord()" v-if="check_access('admin_personal', 'd')" variant="danger" class="mr-2">Eliminar</b-btn></template>
                <template slot="button"><b-btn @click="closeDeleteRecord()" variant="primary">Cerrar</b-btn></template> 
            </sweet-modal>
        </div>

        <b-modal id="modals-top" v-model="showFormulario"  class="modal-center" size="lg">
            <div slot="modal-title">
            Registrar <span class="font-weight-light">Personal</span><br>
            <!-- <small :class="infostatus">El cdigo del centro de trabajo enlaza el equipo que esta siendo monitoreado desde los equipos electrnicos.</small> -->
            </div>
            <fieldset class="demo-vertical-spacing-sm">
                <div>
                    <div class="row">
                        <div class="col-3">

                        </div>
                        <div class="col-9">        
                            <b-form-group :feedback="objState.message" label="Nombre" horizontal>
                                <b-input placeholder="Nombre" v-model.trim="objInfo.name" :state="objState.name!=''" v-on:input="objState.name = 'valid'; objState.name= objInfo.name==''?'invalid':'valid'"/>
                            </b-form-group>
                            <b-form-group :feedback="objState.message" label="Apellido" horizontal>
                                <b-input placeholder="Apellido" v-model.trim="objInfo.lastname" :state="objState.lastname!=''" v-on:input="objState.lastname = 'valid'; objState.lastname= objInfo.lastname==''?'invalid':'valid'"/>
                            </b-form-group>
                        </div>
                    </div>
                </div>
                <b-form-group :feedback="objState.message" label="Cdigo" horizontal>
                    <b-input placeholder="Cdigo" v-model.trim="objInfo.code" :state="objState.code!=''" v-on:input="objState.code = 'valid'; objState.code= objInfo.code==''?'invalid':'valid'"/>
                </b-form-group>

                <b-form-group :feedback="objState.message" label="Nmero de Identificacin" horizontal>
                    <b-input placeholder="Nmero de Identificacin" v-model.trim="objInfo.id_number" :state="objState.id_number!=''" v-on:input="objState.id_number = 'valid'; objState.id_number= objInfo.id_number==''?'invalid':'valid'"/>
                </b-form-group>

                <b-form-group label="rea Funcional" horizontal :feedback="objState.functional_areas" >
                    <multiselect
                        v-model="objInfo.functional_areas"
                        label="description"
                        track-by="description"
                        placeholder=""
                        :options="tablefunctional_areas"
                        :searchable="true"
                        :hide-selected="true">
                        <span slot="noResult">No found.</span>
                    </multiselect>
                </b-form-group>

                <b-form-group :feedback="objState.message" label="Cargo" horizontal>
                    <b-input placeholder="Cargo" v-model.trim="objInfo.position"/>
                </b-form-group>

                <h3>Detalles</h3>
                <b-form-group :feedback="objState.message" label="Fecha de Nacimiento" horizontal>
                    <datepicker
                        v-model="objInfo.birthdate"
                        :bootstrapStyling="true"
                        :monday-first="true"
                        :full-month-name="true"
                        placeholder="Fecha de Nacimiento"
                        :calendar-button="true"
                        calendar-button-icon="ion ion-md-calendar"
                        :clear-button="true"/>
                </b-form-group>
                 
                <b-form-group :feedback="objState.message" label="Fecha de Ingreso" horizontal>
                    <datepicker
                        v-model="objInfo.date_of_admission"
                        :bootstrapStyling="true"
                        :monday-first="true"
                        :full-month-name="true"
                        placeholder="Fecha de Ingreso"
                        :calendar-button="true"
                        calendar-button-icon="ion ion-md-calendar"
                        :clear-button="true"/>
                </b-form-group>

                 <b-form-group :feedback="objState.message" label="Salario" horizontal>
                    <b-input placeholder="Salario" v-model.trim="objInfo.salary"/>
                </b-form-group>
                    
            </fieldset>
            <div slot="modal-footer" class="w-100 text-right">
                <b-btn variant="primary" @click="cancel()">Cancelar</b-btn>
                <b-btn :variant="variantSaveButton" v-if="check_access('admin_personal', 'c')"  @click="submit()">Guardar</b-btn>
            </div>
        </b-modal>

        <b-modal id="modals-top" v-model="showDetails"  class="modal-center" size="xl">
            <div slot="modal-title">
            Detalles <span class="font-weight-light">Personal</span><br>
            <!-- <small :class="infostatus">El cdigo del centro de trabajo enlaza el equipo que esta siendo monitoreado desde los equipos electrnicos.</small> -->
            </div>
            <fieldset class="demo-vertical-spacing-sm">
                <div class="row">
                    <div class="col-5">
                        <b-form-group :feedback="objState.message" label="" horizontal>
                            <datepicker
                                v-model="objTracking.start_date"
                                :bootstrapStyling="true"
                                :monday-first="true"
                                :full-month-name="true"
                                placeholder="Fecha de Incial"
                                :calendar-button="true"
                                calendar-button-icon="ion ion-md-calendar"
                                :clear-button="true"/>
                        </b-form-group>
                    </div>
                    <div class="col-5">
                        <b-form-group :feedback="objState.message" label="" horizontal>
                            <datepicker
                                v-model="objTracking.finish_date"
                                :bootstrapStyling="true"
                                :monday-first="true"
                                :full-month-name="true"
                                placeholder="Fecha de Final"
                                :calendar-button="true"
                                calendar-button-icon="ion ion-md-calendar"
                                :clear-button="true"/>
                        </b-form-group>
                    </div>
                    <div class="col-2">
                        <b-btn variant="success" block @click="checkTraking()">Buscar</b-btn>
                    </div>
               </div>
            </fieldset>
            <b-table small show-empty stacked="md" :items="infoTracking" :fields="titlesTracking" class="mt-2">
            </b-table>

            <div slot="modal-footer" class="w-100 text-right">
                <b-btn variant="primary" @click="showDetails=false">Cerrar</b-btn>
            </div>

        </b-modal>

        <sweet-modal ref="longModal">
            <div class="text-left">
                <vue-markdown :source="contentWiki" :anchor-attributes="anchorAttrs"></vue-markdown>
            </div>
        </sweet-modal>

    </div>
</template>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style src="@/vendor/libs/vuejs-datepicker/vuejs-datepicker.scss" lang="scss"></style>
<style src="@/vendor/libs/sweet-modal-vue/sweet-modal-vue.scss" lang="scss"></style>
<style src="@/vendor/libs/vue-notification/vue-notification.scss" lang="scss"></style>

<script>
    import Vue from 'vue'
    import { SweetModal, SweetModalTab } from 'sweet-modal-vue'
    import Multiselect from 'vue-multiselect'
    import { infosysconfig } from "@/components/i40/js/sysconfig";
    import { infomaster } from "@/components/i40/js/master";
    
    import { ApiWikiHelp } from "@/vendor/sbx/sbx-cloud/cloud_apiwiki";

    import Notifications from 'vue-notification'
    import VueMarkdown from 'vue-markdown'
    import moment from 'moment'
    import Datepicker from 'vuejs-datepicker'
    import {infousers} from '@/components/i40/js/users'
  
    Vue.use(Notifications)

    export default {
    
        metaInfo: {
            title: 'Registrar Operario'
        },
        components: {
        Datepicker,
            VueMarkdown,
            Multiselect,
            SweetModal,
            SweetModalTab
        },
        data: () => ({
            name: 'operator',
            version: 'v1.0.0.0',
            showDetails:false,
            tablefunctional_areas:[],
            contentWiki: "",
            anchorAttrs: {
                target: '_blank',
                rel: 'noopener noreferrer nofollow'
            },

            ///Info de filtro y paginas para la tabla
                currentPage: 1,
                perPage: 50,
                totalRows: 0,
                pageOptions: [ 50, 100, 200 ],
                filter: null,
            ///Info de filtro y paginas para la tabla


            showFormulario:false,

            variantSaveButton:"success",
            disabledcode:false,
            modified:false,
            tableData: [],            


            /**
            columns: [
                {key:'description', label:"Descripcion"},
                {key:'workstation_plant_id', label:"workstation_plant_id", thClass: 'd-none', tdClass: 'd-none'},
            ], 
             */

            /// Columnas de las tablas
                columns: [
                    {key:'code', label:"Cdigo"},
                    {key:'name', label:"Nombre"},
                    {key:'lastname', label:"Apellido"},
                    {key:'id_number', label:"Nmero de Identificacin"},
                    // {key:'birthdate', label:"Fecha de Cumpleaos"},
                    {key:'functional_area_code', label:"Departamento"},
                    {key:'position', label:"Cargo"},
                    // {key:'salary', label:"Salario"},
                    // {key:'date_of_admission', label:"Fecha de Ingreso"},
                
                    // {key:'decimal_quantity', label:"Cifras Decimales"},
                    ///Este objeto es fijo en caso de que se necesite modificar la info
                    {key:'edit', label:"Edit"}
                ],
            /// Columnas de las tablas

            /// Objeto que ser enviado en las operacion CRUD
                objInfo: {
                    code_id:"",
                    code:"", 
                    description:"", 
                    decimal_quantity:0,
                    
                },
                objState:{},
            ///

            objTracking:{
                personal_id:null,
                start_date:null,
                finish_date:null,
            },

            infoTracking:[],
            titlesTracking:[
                {key:'code', label:"Cdigo"},
                {key:'start_date', label:"Fecha de Inicio"},
                {key:'finish_date', label:"Fecha Fin"},
                {key:'minutes', label:"Minutos"},],

        }),
        methods:{
            showTracking(item){
                this.objTracking = {
                    personal_id:item.personal_id,
                    start_date:null,
                    finish_date:null,
                }

                this.showDetails=true

            },
            checkTraking(){
                infomaster.personal(this.objTracking,"tracking").then(data => {
                    if(data.data!= ""){
                        this.infoTracking = data.data
                    }else{
                        this.infoTracking = []
                    }
                })
            },
            
            check_access(control, action){
            return infousers.check_access(control,action)
            },
        ///Funcion para reiniciar los datos
            restartInfo(){
                 this.objInfo= {
                }
            },
        ///Funcion para reiniciar los datos

        ///Funcion para cargar la ayuda del helper
             openHelp: function(){
                //  this.contentWiki = "# FFFF"
                ApiWikiHelp(this.version, this.name).then(data => {
                    this.contentWiki = data.data
                })
                .catch(data =>{
                    this.contentWiki = data.data
                })
                this.$refs.longModal.open()
            },
        ///Funcion para cargar la ayuda del helper

        ///Filtros Para las Tablas
            onFiltered (filteredItems) {
                this.totalRows = filteredItems.length
                this.currentPage = 1
            },
        ///Filtros Para las Tablas

        ///Funcion para envio de alertas lateral superior derecha
            showCustom: function(classes, title, text) {
                this.$notify({
                    group: 'notifications-default',
                    type: classes,
                    title: title,
                    text: text
                })
            },
        /// Funcion para envio de alertas lateral superior derecha

        ///Adicionar un nuevo dato, cancelar el ingreso, cancelar el borrado de un dato
            addData:function(){
                this.restartInfo()
                this.showFormulario=true
            },

            cancel: function(){
                this.restartInfo()
                this.showFormulario=false
            },

            closeDeleteRecord: function(){
                this.restartInfo()
                this.$refs.errorWithButtonAlert.close()
            },
        ///Adicionar un nuevo dato, cancelar el ingreso, cancelar el borrado de un dato

        ///Eventos CRUD
            submit: function(){
                this.objInfo.functional_area_id=this.objInfo.functional_areas.functional_area_id
                // console.log(this.objInfo)
                if(this.validate_content()){
                    var today = new Date();
                    var dd = String(today.getDate()).padStart(2, '0');
                    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
                    var yyyy = today.getFullYear();

                    today = yyyy+'-'+mm+'-'+dd;

                    this.objInfo.salary = this.objInfo.salary=='' || this.objInfo.salary==null ? 0:this.objInfo.salary
                    this.objInfo.date_of_admission=this.objInfo.date_of_admission=='' || this.objInfo.date_of_admission==null ? today:this.objInfo.date_of_admission
                    this.objInfo.birthdate=this.objInfo.birthdate=='' || this.objInfo.birthdate==null ? today:this.objInfo.birthdate
                    
                    infomaster.personal(this.objInfo, this.objInfo.personal_id === "" || this.objInfo.personal_id === undefined ? "insert" : "update").then(data => {
                        if (data.status === 202){
                            this.showCustom('bg-danger text-white', "Error","Error Guardando el Registro!")
                        }else{
                            if(this.objInfo.personal_id === "")
                                this.showCustom('bg-success text-white', "Guardar registro","El registro se ha Guardado con xito!")
                            else
                                this.showCustom('bg-warning text-white', "Modificar registro","El registro se ha Modificado con xito!")
                            this.showData()
                            this.cancel()   
                        }
                    })
                    .catch(function (error) {
                        console.log(error.status)
                    })
                    }else{
                    this.showCustom('bg-danger text-white', "Error","La Informacin esta Incompleta, Por Favor Valide!")
                }
            },

            deleteRecord: function(){
                infomaster.personal(this.objInfo, "delete").then(data => {
                    if (data.status === 202){
                        this.showCustom('bg-danger text-white', "Error","Error al Eliminar el Registro!")
                    }else{
                        this.showCustom('bg-success text-white', "Registro Eliminado","El Registro se ha Eliminado con xito!")
                        this.restartInfo()
                    }
                    this.dismissCountDown = this.dismissSecs
                    this.showData()
                })
                .catch(function (error) {
                    console.log(error.status)
                })
                
                this.$refs.errorWithButtonAlert.close()
            },

            showData: function(){
                infomaster.personal([],"select").then(data => {
                    if(data.data != ""){
                        this.tableData = data.data
                        this.totalRows= this.tableData.length
                    }else{
                        this.tableData = []
                        this.totalRows= 0
                    }
                })
            },
        ///Eventos CRUD

        ///Preracion de datos para el borrado y el modificado desde el evento de la tabla
            data_change :function(row,action){
                this.variantSaveButton="warning"

                ////Informacion que ser modificada o borrada
                this.objInfo=row
                
                if(action === "edit"){
                    this.showFormulario=true
                    this.objInfo.code_id = row.code
                }
                else
                    this.$refs.errorWithButtonAlert.open()
            },
        ///Preracion de datos para el borrado y el modificado desde el evento de la tabla
             validate_content(){
                let isOK = true;
                this.objState = {}
                this.objState.message = 'Este es un Campo Obligatorio'

                if (this.objInfo.name == ''){
                    this.objState.name = 'invalid'
                    isOK = false;
                }else{
                    this.objState.name = 'valid'
                }
                if (this.objInfo.lastname == ''){
                     this.objState.lastname = 'invalid'
                     isOK = false;
                }else{
                    this.objState.lastname = 'valid'
                }
                if (this.objInfo.code == ''){
                     this.objState.code = 'invalid'
                     isOK = false;
                }else{
                    this.objState.code = 'valid'
                }
                if (this.objInfo.id_number == ''){
                     this.objState.id_number = 'invalid'
                     isOK = false;
                }else{
                    this.objState.id_number = 'valid'
                }
                return isOK
            },

        ///Espacio para metodos propios del control
        ///Espacio para metodos propios del control
        
        },
        created(){
            infosysconfig.functionalarea([],"select").then(data => {
                if(data.data!= ""){
                    this.tablefunctional_areas = data.data
                }
            })

            this.showData()
        },
    }
</script>